$backgroundColor: #ffffff;
$bodyColor: #000000;
$bodyFont: -apple-system,BlinkMacSystemFont,"Segoe UI",Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";

body {
  background: $backgroundColor;
  color: $bodyColor;
  font-family: $bodyFont;
  width: 75%;
  max-width: 500px;
  margin: 0 auto;

  @media (max-width: 820px) {
    width: 90%;
  }
}

.case-study {
  h2 {
    margin-top: 2.6rem;
  }

  h3 {
    margin-top: 2.4rem;
  }

  // h2 + h3 {
  //   margin-top: 1.2rem;
  // }

  .design-principle {
    border: 1px solid $bodyColor;
    padding: 1.2rem;
    border-radius: 0.4rem;
    margin: 1.4rem 0;
  }
}

// temporary styling 
main {
  margin-top: 4em;

  p {
    line-height: 1.6;
  }

  li {
    padding: 0.5em 0;
  }

  hr {
    margin: 3em 0;
  }

  .last-updated {
    text-align: center; 

    p {
      margin: 0.25em;
      color: rgba($bodyColor, 0.5);
      font-style: italic;
      font-size: 0.8em;
    }
    
    a {
      color: rgba($bodyColor, 0.5);
    }

    img {
      width: 30px;
    }
  }

  .under-construction {
    margin: 0 auto;
  }
}
